.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.hidden {
  display: none !important; }

.visible {
  display: flex; }

.page {
  margin: 0;
  padding: 0; }

.page-body {
  position: relative;
  margin: 0;
  padding: 0;
  font-family: "Futura PT", "Arial", sans-serif; }
  .page-body--bg::before {
    content: "";
    position: absolute;
    width: 49%;
    height: 670px;
    top: 77px;
    right: 0;
    background-image: url("../img/express-print-img.jpg");
    background-position: 0 0;
    background-repeat: no-repeat;
    background-size: cover; }

.main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start; }

.header {
  font-family: "Futura PT";
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  margin-top: 21px;
  margin-bottom: 21px; }
  .header .request {
    display: flex;
    width: 165px;
    height: 100%;
    border-radius: 5px;
    justify-content: center;
    align-items: center;
    color: #fff;
    background-color: #183A37;
    text-decoration: none; }

.main-nav {
  font-family: "Futura PT";
  margin-right: 17px;
  margin-left: 20px; }

.main-nav__link {
  color: #04151f;
  text-decoration: none;
  font-weight: normal;
  font-size: 18px;
  line-height: 23px;
  padding: 10px; }
  .main-nav__link--active {
    color: #CB000D; }

.contacts-links {
  margin-right: 27px; }

.contacts-link {
  position: relative;
  color: #04151F;
  font-size: 18px;
  line-height: 23px;
  text-decoration: none;
  margin-left: 30px; }
  .contacts-link::before {
    position: absolute;
    content: "";
    width: 24px;
    height: 24px;
    top: 0;
    left: -28px;
    background-repeat: no-repeat;
    background-position: 0 0; }

.contacts-link--tel::before {
  background-image: url("../img/phone-call.svg"); }

.contacts-link--mail {
  margin-right: 18px; }
  .contacts-link--mail::before {
    background-image: url("../img/e-mail.svg"); }

.contacts-link--adress {
  margin-left: 40px; }
  .contacts-link--adress::before {
    background-image: url("../img/adress.svg"); }

.express-print {
  font-family: "Futura PT";
  background-color: #F6F6F6;
  min-height: 670px; }

.express-print__container {
  display: flex;
  justify-content: space-between; }

.express-print__title {
  font-weight: 600;
  font-size: 80px;
  line-height: 60px; }

.express-print__sub-title {
  font-weight: 600;
  font-size: 58px;
  line-height: 70px; }

.express-print__text {
  font-weight: 450;
  font-size: 35px;
  line-height: 45px; }

.express-print__get-price {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 600;
  font-size: 20px;
  line-height: 26px;
  background-color: #CB000D;
  text-align: center;
  color: #fff;
  text-decoration: none;
  width: 210px;
  height: 54px;
  border-radius: 5px; }

.director {
  font-family: "Futura PT";
  display: flex;
  min-height: 703px;
  background-image: linear-gradient(90deg, #183A37 0%, #183A37 42%, #fff 42%, #fff 100%);
  padding-bottom: 22px; }

.director__container {
  display: flex; }

.director__block-left {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  position: relative;
  color: #fff;
  font-style: normal;
  font-size: 22px;
  line-height: 24px;
  margin-left: 180px; }
  .director__block-left::before {
    position: absolute;
    content: "";
    top: -100px;
    right: -130px;
    width: 470px;
    height: 703px;
    background-image: url("../img/director-img.jpg");
    background-position: 0 0;
    background-repeat: no-repeat; }

.director__name {
  font-weight: 600;
  margin-bottom: 3px; }

.director__sub-title {
  font-weight: 400; }

.director__block-right {
  display: flex;
  flex-direction: column;
  margin-left: auto;
  margin-right: 0;
  width: 570px;
  color: #04151F;
  font-style: normal; }
  .director__block-right p {
    font-weight: normal;
    font-size: 20px;
    line-height: 24px; }

.director__word {
  font-weight: 600;
  font-size: 50px;
  line-height: 52px; }

.features {
