.container {
  margin-left: auto;
  margin-right: auto;
  padding: 0 20px;
  width: 100%;

  @media (min-width: 768px) {
    max-width: 708px;
    padding: 0 30px;
  }

  @media (min-width: 1024px) {
    max-width: 944px;
    padding: 0 30px;
  }

  @media (min-width: 1300px) {
    max-width: 1200px;
    padding: 0 15px;
  }

  // @media (min-width: 1900px) {
  //   max-width: 1800px;
  // }
}

